@import "../../GLOBAL_VARIABLES.scss";

.modal {
    width: 100vw;
    height: 100vh;
    left: 0;
    top: 0;
    position: fixed;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.modalContent {
    background: $backgroundColor;
    padding: 10px 15px;
    cursor: auto;
}
